.movie-data-contain {
    display: flex;
    flex-wrap: wrap;
    /* justify-content: space-between; */
}

.bolly-container {
    display: flex;
    flex-wrap: wrap;
}

.dataContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.post-title {
    /* background-color: blue; */
    font-size: 32px;
    font-weight: 700;
    color: #050117;
    text-shadow: 3px 3px 12px #fa14b5;
}

.left-data-link {
    /* position: relative; */
    width: 40%;
}

.bolly-all-image {
    border-radius: 10px;
    box-shadow: 4px 4px 8px #262626;
    border: 1px solid #f2f2f2;
}

.top-post-image {
    height: 30vh;
    width: 100%;
    border-radius: 8px;
    border: 1px solid #fff;
    box-shadow: 4px 4px 8px #262626;
}

/* .top-post-image {
    width: 100%;
    height: 18vh;
    position: relative;
    cursor: pointer;
    border-radius: 8px;
} */

.line {
    height: 2px;
    background-color: red;
    width: 120px;
    margin-bottom: 5px;
}

.right-data-link {
    width: 10rem;
}

.right-data-wrapper {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    margin-top: 10px;
    color: green;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.line2 {
    height: 2px;
    background-color: red;
    width: 80px;
}

.BollywoodData {
    width: 90%;
    height: auto;
    position: relative;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 1.1rem;
    color: green;
    font-family: sans-serif;
    /* margin-bottom: 10px; */
    padding: 5px;
    /* background-color: red; */
}

.bitween {
    background: linear-gradient(yellow, orange, rgb(11, 27, 249));
    height: 2px;
    margin-top: 10px;
}

.top-post-details {
    color: #050117;
}

.top-bolly-post-underline {
    width: 15rem;
    height: 3px;
    background-color: red;
    cursor: pointer;
    margin-left: 50px;
}
.top-bolly-post-underline2 {
    width: 10rem;
    height: 3px;
    background-color: red;
    cursor: pointer;
    margin-top: 10px;
    margin-left: 120px;
}

.top-post-details-wrapper .title {
    font-weight: 700;
    font-size: 18px;
}

.bolly-top-img {
    margin-top: 25px;
    margin-bottom: 25px;
}

.bolly-title {
    font-size: 18px;
    font-weight: 700;
    margin-top: 0;
}

.IMdb {
    font-size: 17px;
    font-weight: 500;
    margin-top: 0;
}

.desc {
    margin-top: 0;
}

.release-left {
    margin-top: 0;
    font-size: 18px;
}

.bolly-ads-container {
    /* background-color: red; */
    width: 70%;
    height: 70%;
}

.bolly-ads-box {
    width: 300px;
    height: 300px;
    border: 1px solid rgb(255, 19, 19);
    border-radius: 10px;
    justify-content: center;
    padding: 10px;
    margin-top: 10px;
}

.movie-details-para {
    margin-left: 30px;
}

.movie-details-release-para {
    margin-left: 30px;
}

@media screen and (max-width:1024px){
      
    /* .bolly-container {
        display: block;
    } */
    
    
    .bolly-all-image {
        border-radius: 10px;
        box-shadow: 4px 4px 8px #262626;
        border: 1px solid #f2f2f2;
        width: 400px;
        height: 300px;
    }
    .bolly-top-img {
        margin-top: 5px;
        margin-bottom: 5px;
    }
    .BollywoodData {
        width: 90%;
        height: auto;
        position: relative;
        display: block; 
    }
    .left-data {
        width: 100%;
        height: auto;
        /* background-color: red; */
      }
      .bolly-wrapper {
        width: 100%;
        /* margin-bottom: 2px; */
        /* margin-top: 0%;
        margin-bottom: 0%; */
        /* background-color: red; */
      }
      .bitween {
        background: linear-gradient(yellow, orange, rgb(11, 27, 249));
        height: 2px;
        margin-top: 2px;
    }
    .bolly-title {        
        margin-top: 20px;
    }
    .desc {
        margin-top: 10px;
    }
    .movie-data-contain {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
    }
}